export class Serializable {
    fillFromJSON(json: string) {
        var jsonObj = JSON.parse(json).array.forEach(element => {
            console.log(element);
        });
        for (var propName in jsonObj) {
            this[propName] = jsonObj[propName]
        }
    }
}

export class UsersModel extends Serializable {
    _id: string;
    role: string;
    isDeleted: boolean;
    updatedOnUTC: string;
    createdOnUTC: string;
    email: string;
    userDisplayName: string;
    password: string;
    channel: string;
    lastName: string;
    firstName: string;
    __v: number;
    pictureUrl: string;
    agencyName:string;
    constructor(Id,
        Role,
        IsDeleted,
        UpdatedOnUTC,
        CreatedOnUTC,
        Email,
        UserDisplayName
    ) {
        super();
        this._id = Id;
        this.role = Role;
        this.isDeleted = IsDeleted;
        this.updatedOnUTC = UpdatedOnUTC;
        this.createdOnUTC = CreatedOnUTC;
        this.email = Email;
        this.userDisplayName = UserDisplayName;
    }
    GetUserName(): string { return this.userDisplayName }
    GetRole(): string { return this.role }
    GetId(): string { return this._id }
    GetIsDeleted(): boolean { return this.isDeleted }
    GetEmail(): string { return this.email }
    GetCreatedOnUTC(): string { return this.createdOnUTC }
    GetUpdatedOnUTC(): string { return this.updatedOnUTC }
}



