import { Component, ViewEncapsulation } from '@angular/core';
import { Input } from '@angular/core';
import { UsersService } from '../services/UsersService';
import { LocalStorageService } from 'angular2-localstorage/LocalStorageEmitter';
import { SessionStorage } from 'angular2-localstorage/WebStorage';
import { Router } from '@angular/router';
import { UsersModel } from '../models/UsersModel';
declare var jQuery: any;

@Component({
  selector: 'login',
  styles: [require('./login.style.scss')],
  template: require('./login.template.html'),
  providers: [UsersService, LocalStorageService],
  encapsulation: ViewEncapsulation.None,
  host: {
    class: 'login-page app'
  }
})
export class Login {
  @Input() Email: string = "";
  @Input() Password: string;
  @SessionStorage() public userId: string;
  @SessionStorage() public role: string;
  @SessionStorage() public userObject: UsersModel;
  @SessionStorage() public token: string;
  public userService: UsersService;
  public router: any;

  constructor(public _userService: UsersService, storageService: LocalStorageService, router: Router) {

    this.router = router;

    this._userService.authenticateToken().subscribe(
         a=>{
             this.token=a.token;

         }
       );
  }
 
  onKey(event) {
    if (event.keyCode == 13) {
      jQuery('#wrongLogin').hide();
      jQuery('#enterEmail').hide();
      jQuery('#enterPassword').hide();
      if (this.Email == "") {
        jQuery('#enterEmail').show();
      }
      if (this.Password == "" || this.Password === undefined) {
        jQuery('#enterPassword').show();
      }
      if (this.Email != "" && this.Password != "") {
        jQuery('#loader').show();
        jQuery('#btns').hide();

        this._userService.authenticateAdminUser(this.Email, this.Password,this.token).subscribe(
          a => {
            if (a.code == 200) {
              this.userId = a.data;
              this.userId = a.data._id;
              this.role = a.data.role;
              this.userObject = a.data;
              
              this.router.navigate(['/app/dashboard']);
              jQuery('#btns').show();
              jQuery('#loader').hide();
            }
            else {
              jQuery('#btns').show();
              jQuery('#loader').hide();
              jQuery('#wrongLogin').show();

            }
          }
        );
      }
    }
  }



  onSubmit() {
    jQuery('#wrongLogin').hide();
    jQuery('#enterEmail').hide();
    jQuery('#enterPassword').hide();
    if (this.Email == "") {
      jQuery('#enterEmail').show();
    }
    if (this.Password == "" || this.Password === undefined) {
      jQuery('#enterPassword').show();
    }
    if (this.Email != "" && this.Password != "") {
      jQuery('#loader').show();
      jQuery('#btns').hide();
      
      this._userService.authenticateAdminUser(this.Email, this.Password,this.token).subscribe(
        a => {
          if (a.code == 200) {
            this.userId = a.data;
            this.userId = a.data._id;
            this.role = a.data.role;
            this.userObject = a.data;
            this.router.navigate(['/app/dashboard']);
            jQuery('#btns').show();
            jQuery('#loader').hide();
          }
          else {
            jQuery('#btns').show();
            jQuery('#loader').hide();
            jQuery('#wrongLogin').show();

          }
        }
      );
    }
  }
}



